@charset "UTF-8";
/*  Main directory wrapper */
.uw-directory {
  padding-bottom: 160px;
  position: relative;
  z-index: 2;
  background: white;
  /* Layout for filter/search boxes */
  /* Category button styling */
  /* Search box styling */
  /* Directory container */
  /* Updated uw-card styling */
  /* Responsive fallback */
  /* Modal for profile view */
  /* link style */
  /* Table list view styling */
}
.uw-directory .section-label {
  display: block;
  margin-bottom: 0px;
  font-weight: 600;
  font-size: 15px;
  color: #4b2e83;
  font-family: "Encode Sans Compressed", sans-serif;
}
.uw-directory .section-label small {
  font-weight: normal;
  color: #666;
}
.uw-directory .view-toggle .fa-solid {
  margin-right: 10px;
}
.uw-directory .h3 {
  font-weight: 800;
  font-size: 24px;
  color: #4b2e83;
  margin-top: 0px;
}
.uw-directory .h2 {
  font-size: 24px;
  font-weight: bold;
  color: white;
}
.uw-directory .title {
  color: white;
  font-weight: bold;
  font-size: 16px;
}
.uw-directory .department {
  color: white;
  font-size: 16px;
}
.uw-directory .email {
  color: white;
  font-weight: bold;
  font-size: 16px;
}
.uw-directory .custom-dropdown {
  width: 240px;
  font-weight: bold;
  position: relative;
}
.uw-directory .custom-btn {
  display: flex;
  align-items: center;
  width: 100%;
  height: 48px;
  padding: 0;
  background-color: white;
  box-shadow: none;
  border: none;
  justify-content: space-between;
}
.uw-directory .label-text {
  text-align: left;
  padding: 0 12px;
  line-height: 48px;
  font-size: 14px;
  color: #4b2e83;
  border: 1px solid #e8e3d3;
  border-right: none;
  height: 48px;
  background-color: white;
  flex: 1 1 auto;
  overflow: hidden;
  text-overflow: ellipsis; /* … when department name is huge  */
  white-space: nowrap;
}
.uw-directory .custom-dropdown .custom-btn .arrow-block {
  flex: 0 0 auto; /* never shrinks                   */
}
.uw-directory .arrow-block {
  width: 49px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #4b2e83;
  color: white;
  font-size: 16px;
  border-left: none;
}
.uw-directory .custom-btn:hover .label-text {
  background-color: #4b2e83;
  border: 0px solid #e8e3d3;
  color: white;
}
.uw-directory .custom-btn:hover .arrow-block {
  background-color: #85754d;
}
.uw-directory .custom-dropdown .dropdown-menu {
  border-radius: 0;
  margin-top: 0;
  width: 100%;
  min-width: 240px;
  border: 1px solid #ccc;
  box-shadow: none;
  position: absolute;
  z-index: 1000;
}
.uw-directory .dropdown-item:hover {
  background-color: #f3f3f3;
  color: #4b2e83;
}
.uw-directory .view-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 48px;
  font-weight: 600;
  padding: 0 12px;
  border: none;
  font-size: 14px;
}
.uw-directory .view-btn.active {
  background-color: #4b2e83;
  color: #ffffff;
}
.uw-directory .tab-view:hover {
  background-color: #85754d;
  color: #ffffff;
}
.uw-directory .tab-view {
  background-color: #e8e3d3;
  color: #4b2e83;
}
.uw-directory .view-btn::after {
  display: none !important;
}
.uw-directory .filter,
.uw-directory .search {
  display: flex;
  justify-content: left;
  align-items: center;
  height: 100%;
}
.uw-directory .toggle-view {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
.uw-directory .toggle-view .dropdown-menu {
  background-color: #ffffff;
  min-width: 110px;
  margin-top: 10px;
}
.uw-directory .toggle-view .dropdown-item,
.uw-directory .dropdown-item {
  display: flex;
  align-items: center;
  justify-content: center;
}
.uw-directory .dropdown-menu {
  min-width: 100% !important;
  white-space: normal !important;
  word-break: break-word !important;
  overflow-wrap: break-word !important;
}
.uw-directory .dropdown-menu .dropdown-item {
  display: flex !important;
  align-items: center !important;
  justify-content: flex-start !important;
  height: auto !important;
  padding: 8px 16px !important;
  font-size: 14px !important;
  font-weight: 500 !important;
  color: #333 !important;
  background-color: #fff !important;
  border: none !important;
  white-space: normal !important;
  word-break: break-word !important;
  box-sizing: border-box !important;
}
.uw-directory .dropdown-menu .dropdown-item:hover {
  background-color: #ece6f0 !important;
}
.uw-directory .folklore-container {
  display: flex;
  flex-wrap: wrap;
  margin: 20px 0;
  align-items: center;
}
.uw-directory .folklore-inner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}
.uw-directory .folklore-box {
  margin-right: 10px;
  margin-bottom: 10px;
  height: 100%;
  align-items: flex-start;
  flex-direction: column;
}
.uw-directory .folklore-box.filter {
  width: 164px;
  position: relative;
}
.uw-directory .folklore-box.search {
  width: 362px;
}
.uw-directory .folklore-box.view-toggle {
  flex-grow: 1;
  align-items: flex-end;
  justify-content: flex-start;
}
@media (max-width: 768px) {
  .uw-directory .folklore-box {
    width: 100% !important;
    justify-content: flex-start;
  }
  .uw-directory .folklore-box.view-toggle {
    justify-content: flex-start;
  }
}
.uw-directory .btn {
  margin: 0;
}
.uw-directory .btn.btn-sm {
  padding: 0;
  font-size: 0.875rem;
  line-height: 1.5;
  margin: 10px 0;
}
.uw-directory .folklore-box .btn.dropdown.white:focus,
.uw-directory .folklore-box .btn.dropdown.white:active {
  outline: none !important;
  box-shadow: none !important;
  border: none !important;
}
.uw-directory .arrow-box.dropdown-box {
  display: flex;
  justify-content: center;
  align-items: center;
}
.uw-directory .category-label {
  display: flex;
  justify-content: left;
  align-items: center;
  width: 150px;
  height: 48px;
  font-size: 14px;
}
.uw-directory .searchbox {
  position: relative;
  clear: both;
  font-size: 20px;
  color: #7a7a7a;
  font-family: "Encode Sans Compressed", sans-serif;
}
.uw-directory .searchbox #s {
  width: 100%;
  height: 48px;
  padding: 10px;
  font-size: 14px;
  border: 1px solid #e8e3d3;
  color: #4b2e83;
  background-color: white;
}
.uw-directory .searchbox #s:focus {
  outline: none;
  border: 1px solid #e8e3d3;
  box-shadow: none;
}
.uw-directory .searchbox #searchsubmit {
  position: absolute;
  right: 0;
  top: 0;
  height: 48px;
  width: 48px;
  border: none;
  cursor: pointer;
  background: url("./svg/search.svg") no-repeat center center #4b2e83;
  background-size: 22px;
}
.uw-directory .searchbox #searchsubmit:hover, .uw-directory .searchbox #searchsubmit:focus {
  background-color: #85754d;
  border: 0px solid #050505;
  box-shadow: none;
  outline: none;
}
.uw-directory #directory-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-between;
  position: relative;
}
.uw-directory .uw-card {
  position: static !important;
  flex: 1 1 calc(50% - 20px);
  max-width: calc(50% - 20px);
  min-width: 320px;
  height: 255px;
  display: flex;
  background-color: #4b2e83;
  color: white;
  margin: 0;
  box-sizing: border-box;
}
.uw-directory .uw-card-img {
  width: 228px;
  height: 255px;
  object-fit: cover;
  object-position: center;
}
.uw-directory .uw-card-text {
  margin-left: 40px;
  display: flex;
  margin-top: 10px;
  align-items: center;
  justify-content: left;
  width: calc(100% - 228px - 40px);
}
.uw-directory .uw-card-text span h3,
.uw-directory .uw-card-text p {
  color: white;
  font-size: 16px;
  margin: 0;
  padding: 2px 0;
}
.uw-directory .uw-card-text span h3 {
  font-size: 24px;
  font-weight: bold;
}
@media (max-width: 768px) {
  .uw-directory .uw-card {
    flex: 1 1 100%;
    max-width: 100%;
    flex-direction: column;
    height: auto;
  }
  .uw-directory .uw-card-img {
    width: 100%;
    height: auto;
  }
  .uw-directory .uw-card-text {
    margin: 20px;
    width: auto;
    flex-direction: column;
    align-items: flex-start;
  }
}
.uw-directory .uw-modal {
  display: none;
  position: fixed;
  z-index: 10000;
  padding-top: 80px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.6);
}
.uw-directory .uw-modal-content {
  background-color: white;
  margin: auto;
  width: 90%;
  max-width: 900px;
  position: relative;
  font-family: "Open Sans", sans-serif;
}
.uw-directory .uw-modal-close {
  position: absolute;
  top: -19px;
  right: 30px;
  font-weight: bold;
  font-size: 28px;
  color: #666;
  cursor: pointer;
  /* height: 53px; */
  /* background: #fff; */
  border: 0px solid #ccc;
  border-radius: 4px;
}
.uw-directory .uw-modal-flex {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.uw-directory .uw-modal-img-col {
  flex: 0 0 270px;
}
.uw-directory .uw-modal-img-col img {
  height: 100%;
  object-fit: cover;
}
.uw-directory .uw-modal-text-col {
  flex: 1;
  padding: 25px;
}
.uw-directory .uw-modal-text-col h4 {
  margin-bottom: 0;
  font-size: 24px;
  font-weight: bold;
  color: #000;
}
.uw-directory .uw-modal-text-col p {
  font-size: 16px;
  margin: 3px 0;
  color: black;
}
.uw-directory .bold {
  font-weight: bold;
  font-size: 16px;
  color: #000;
}
.uw-directory .light-text {
  color: #000;
  font-size: 16px;
  font-weight: 400;
  margin: 3px 0;
}
.uw-directory .uw-modal-footer {
  background-color: #eee7d9;
  padding: 20px;
  margin: 3px 0;
}
.uw-directory .uw-modal-footer h4,
.uw-directory .uw-modal-footer p {
  color: #4b2e83;
  font-weight: bold;
  margin: 0 0 10px;
}
.uw-directory #modal-bio {
  font-size: 16px;
  font-weight: 400;
  color: black;
}
.uw-directory .closef {
  float: right;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1;
  color: #373a3c;
  text-shadow: 0 1px 0 #fff;
  opacity: 0.5;
  font-size: 1.8rem;
  font-weight: 700;
  /* line-height: 1; */
  color: #373a3c;
  text-shadow: 0 1px 0 #fff;
  float: right;
  position: absolute;
  top: 26px;
  right: 26px;
}
.uw-directory .folklore-modal {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  display: none;
  z-index: 9999;
  justify-content: center;
  align-items: center;
}
.uw-directory .folklore-modal-content {
  background: #fff;
  max-width: 900px;
  width: 90%;
  margin: 40px auto;
  position: relative;
  display: flex;
  flex-direction: column;
}
.uw-directory .folklore-modal-close {
  position: absolute;
  top: -4px;
  right: -1px;
  background: none;
  border: none;
  font-size: 2rem;
  color: #444;
  cursor: pointer;
}
.uw-directory .folklore-modal-body {
  display: flex;
  flex-wrap: wrap;
}
.uw-directory .folklore-modal-left {
  flex: 1 1 250px;
  background-color: #eee7d9;
}
.uw-directory .modal-photo {
  width: 100%;
  height: 394px;
  object-fit: cover;
}
.uw-directory .folklore-modal-right {
  flex: 2 1 500px;
  padding: 2rem;
}
.uw-directory .modal-subtitle {
  font-weight: bold;
  margin-bottom: 0.5rem;
}
.uw-directory .folklore-modal-body .modal-bio p {
  margin: 0 0 0.75rem;
}
.uw-directory .folklore-modal-body .modal-bio { /* allow scrolling if very long */
  max-height: 19vh;
  overflow-y: auto;
}
.uw-directory .folklore-modal-body .modal-bio::-webkit-scrollbar {
  display: none;
}
.uw-directory .results-message {
  font-family: "Encode Sans Compressed", sans-serif;
}
.uw-directory #modal-bio {
  max-height: 6.5em; /* 6 lines   */
  overflow: hidden;
  position: relative;
  transition: max-height 0.25s ease;
}
.uw-directory #modal-bio::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(to bottom, transparent 60%, #fff 95%);
  pointer-events: none;
}
.uw-directory #modal-bio.expanded {
  max-height: none;
}
.uw-directory #modal-bio.expanded::after {
  display: none;
}
.uw-directory .see-more-link {
  display: inline-block;
  margin-top: 0.5rem;
  font-size: 0.875rem;
  font-weight: 600;
  color: #4b2e83;
  cursor: pointer;
}
.uw-directory .modal-dept {
  font-style: italic;
  margin-bottom: 1rem;
}
.uw-directory .modal-bio {
  color: #333;
}
.uw-directory .modal-contact h4 {
  margin-bottom: 0.5rem;
  color: #4b2e83;
}
.uw-directory .modal-contact p {
  margin: 0.25rem 0;
}
.uw-directory .modal-contact i {
  margin-right: 8px;
}
.uw-directory .modal-contact {
  background-color: #eee7d9;
  padding: 2rem;
}
.uw-directory .directory-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  font-family: system-ui, sans-serif;
}
.uw-directory .directory-table th {
  background-color: #4b2e83;
  color: white;
  font-weight: bold;
  text-align: left;
  padding: 14px 16px;
  font-size: 16px;
}
.uw-directory .directory-table td {
  height: 132px;
  padding: 14px 16px;
  font-size: 18px;
  vertical-align: middle;
}
.uw-directory .directory-table tr:nth-child(even) {
  background-color: #fff;
}
.uw-directory .directory-table td:nth-child(2) {
  font-weight: 600;
}
.uw-directory .directory-table img {
  width: 100px;
  height: 100px;
  object-fit: cover;
}
.uw-directory .directory-table tbody tr {
  cursor: pointer;
}
.uw-directory .directory-table tbody tr:hover {
  background-color: #ece6f0;
}
.uw-directory .directory-table tbody tr {
  background: #ffffff;
  transition: background 0.15s ease-in-out;
}
.uw-directory .directory-table tbody tr.even {
  background: #f1f1f6 !important;
}
.uw-directory .directory-table tbody tr:hover {
  background: #ece6f0 !important;
}

.uw-directory.has-sidebar #directory-container {
  display: flex !important;
  flex-wrap: wrap !important;
}
.uw-directory.has-sidebar .uw-card {
  flex: 0 0 100% !important;
  max-width: 100% !important;
  margin-bottom: 1rem !important;
}
.uw-directory.has-sidebar .folklore-box.search {
  width: 300px;
}
.uw-directory.has-sidebar .directory-table img {
  width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}